import React, {useState,useEffect} from 'react';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControl from '@mui/material/FormControl';

import { useSelector, useDispatch } from 'react-redux';
import {
    actionGetMoments,
    actionGetMomentSubjects
} from '../../app/Reducers/moment/momentSlice';


const MomentViewFilter = ({

}) => {

    const dispatch = useDispatch()
    const {userData} = useSelector(state => state.user);
    const {momentSubjects} = useSelector(state => state.moment);

    const [momentFilter, setMomentFilter] = useState([
        {
            label:"Comment",
            checked: false,
            value: 1
        },
        {
            label:"Insight",
            checked: false,
            value: 2
        },
        {
            label:"Challenge",
            checked: false,
            value: 3
        },
        {
            label:"Reflection",
            checked: true,
            value: 4
        }
    ])
    
    const dateFilters = [
        {value:"all",       label:"All"     },
        {value:"daily",     label:"Daily"   },
        {value:"weekly",    label:"Weekly"  },
        {value:"monthly",   label:"Monthly" }
    ]

    const [dateFilter, setDateFilter] = React.useState('all');

    const handleChange = (optionIndex, value) => {
        setMomentFilter([...momentFilter].map(e => {
            return {
                ...e,
                checked: e.value == value ? !e.checked : e.checked
            }
        }))
    }
    const handleDateChange = e => setDateFilter(e.target.value);

    useEffect(() => {
        const momentTypesArray = momentFilter.filter(e=>e.checked).map(e=>e.value)
        dispatch(
            actionGetMoments(
                userData._id,
                {
                    momentTypesArray,
                    dateFilter,
                    page:1
                }
            )
        )
    }, [momentFilter,dateFilter]);

    useEffect(() => {
        dispatch(actionGetMomentSubjects())
    },[])

    return (
        <>
        <FormGroup className="tasks-filter-wrapper">
            <h3 className="filter-by-heading">Filter by type</h3>
            {momentFilter.map(({label,checked,value}, optionIndex) => (
                <FormControlLabel 
                key={optionIndex}
                control={
                    <Checkbox
                    size="small"
                    value={value}
                    checked={checked}
                    onChange={() => handleChange(optionIndex,value)}/>
                }
                label={label} />
            ))}
        </FormGroup>

        <FormGroup className="tasks-filter-wrapper">
            <h3 className="filter-by-heading">Filter by subject</h3>
            {momentSubjects.map((subject, optionIndex) => (
                <FormControlLabel 
                key={optionIndex}
                control={
                    <Checkbox
                    size="small"
                    value={subject}
                    // checked={checked}
                    // onChange={() => handleChange(optionIndex,subject)}
                    />
                }
                label={subject} />
            ))}
        </FormGroup>

        <div className="tasks-filter-wrapper">
            <FormControl >
                <h3 className="filter-by-heading">Filter by date range</h3>
                <RadioGroup
                    name="controlled-radio-buttons-group"
                    value={dateFilter}
                    onChange={handleDateChange}
                >
                    {dateFilters.map(({value,label})=><FormControlLabel
                                                        key={label}
                                                        value={value}
                                                        control={<Radio />}
                                                        label={label} />)}
                </RadioGroup>
            </FormControl>
        </div>
        </>
    )
}

export default MomentViewFilter;