import { createSlice } from '@reduxjs/toolkit';
import Axios from "axios";
import { getHeaders } from '../../helpers.js';

/* Just a test code... remove it afterwards */ 
const URI = "https://reflect-backend-2.azurewebsites.net";//process.env.DEV_MONGO_URL;
/* Just a test code... remove it afterwards */

const initialState = {
    challenges: [],
    momentsList: [],
    momentSubjects: [],

    // list of moment pagination
    hasNextPage: false,
    hasPrevPage: false,
    limit: 10,
    nextPage: null,
    page: 1,
    pagingCounter: 1,
    prevPage: null,
    totalDocs: 0,
    totalPages: 1,

    // list of todo parameters
    momentTypesArray: [4],
    dateFilter: "all",
};

export const momentSlice = createSlice({
    name: 'moment',
    initialState,
    reducers: {
        addMoment: (state,action) => {
            state.noteList = [...state.noteList, action.payload]
        },
        updateMomentsList: (state,action) => {
            console.log(action.payload)

            state.momentsList = action.payload.docs
            state.hasNextPage = action.payload.hasNextPage
            state.hasPrevPage = action.payload.hasPrevPage
            state.limit = action.payload.limit
            state.nextPage = action.payload.nextPage
            state.page = action.payload.page
            state.pagingCounter = action.payload.pagingCounter
            state.prevPage = action.payload.prevPage
            state.totalDocs = action.payload.totalDocs
            state.totalPages = action.payload.totalPages
            state.momentTypesArray = action.payload.momentTypesArray
            state.dateFilter = action.payload.dateFilter
        },
        setMomentSubjects: (state,action) => {
            state.momentSubjects = action.payload
        },
        appendMomentSubjects: (state,action) => {
            if(action?.payload?.length)
            if(state.momentSubjects.findIndex(subject => subject == action.payload) === -1){
                state.momentSubjects = [...state.momentSubjects, action.payload]
            }
        }
    }
});

export const {
    addMoment,
    updateMomentsList,
    setMomentSubjects,
    appendMomentSubjects
} = momentSlice.actions;

export const actionAddMoment = moment => (dispatch,getState) => {
    const {user} = getState();
    const userId = user.userData ? user.userData._id : null;

    const headers = getHeaders();
    if(headers == null) return

    Axios.put(
        `${URI}/moments/add-moment`,
        {
            ...moment,
            userId
        },
        headers
    ).then( res => {
        dispatch(addMoment(res.data))
    })
}

export const actionGetChallenges = () => async (dispatch,getState) => {
    const headers = getHeaders();
    if(headers == null) return

    Axios.get(
        `${URI}/moments/get-challenges`,
        headers
    ).then( res => {
        console.log("actionGetChallenges")
        console.log(res)
    })
}

export const actionGetMoments = (
    userId,
    paramObj
) => async (dispatch,getState) => {
    const headers = getHeaders();
    if(headers == null) return
    const {moment} = getState();

    console.log(paramObj)

    const momentTypesArray = paramObj?.momentTypesArray ? paramObj.momentTypesArray : moment.momentTypesArray
    const dateFilter = paramObj?.dateFilter ? paramObj.dateFilter : moment.dateFilter
    const page = paramObj?.page ? paramObj.page : moment.page

    console.log(`${URI}/moments/get-user-moments/${userId}?momentTypefilter=${momentTypesArray}&dateFilter=${dateFilter}&page=${page}&limit=${10}`)
    Axios.get(
        `${URI}/moments/get-user-moments/${userId}?momentTypefilter=${momentTypesArray}&dateFilter=${dateFilter}&page=${page}&limit=${10}`,
        headers
    ).then( res => {
        // console.log("actionGetMoments")
        dispatch(updateMomentsList({
            ...res.data,
            momentTypesArray,
            dateFilter
        }))
    })
}

export const actionGetMomentSubjects = () => (dispatch,getState) => {
    const headers = getHeaders();
    if(headers == null) return
    Axios.get(
        `${URI}/moments/get-moment-subjects`,
        headers
    ).then( res => {
        dispatch(setMomentSubjects(res.data))
    })
}

export const actionAppendMomentSubjects = subject => dispatch => {
    dispatch(appendMomentSubjects(subject))
}

export default momentSlice.reducer;